.card{
    margin-bottom: 30px;
    margin-right: 10px;
    width: 380px;
    height: 550px;
    border-radius: 0;
    filter: drop-shadow(0 10px 5px rgba(0,0,0,0.125));
    
}

.img{
    display: flex;
    justify-content: center;
    align-items: center;
}

img{
    /* border-radius: 20px; */
    width: 400px;
    height: 230px;
    /* padding: 10px; */
}

.img{
    overflow: hidden;
}
.card img{
    transition: all 1s ease;
}

.card:hover img{
    transform: scale(1.2);
}


.card a{
    width: 100px;
    margin: 0px 0px 20px 15px;
}

a:hover{
    transform: scale(1.1); 
    border-color: rgba(255, 255, 255, 0.9);
    filter: drop-shadow(0 10px 5px rgba(0,0,0,0.125));
    transition: all .3s ease;    
    }

